/* eslint-disable max-len*/
const addFontGaegu = (parent) => {
  parent.append('defs')
    .append('style')
    .attr('type', 'text/css')
    .text(`@font-face {
    font-family: 'gaeguregular';
    src: url(data:application/font-woff2;charset=utf-8;base64,) format('woff2'),
         url(data:application/font-woff;charset=utf-8;base64,) format('woff');
    font-weight: normal;
    font-style: normal;
}`);
};

const addFontIndieFlower = (parent) => {
  parent.append('defs')
    .append('style')
    .attr('type', 'text/css')
    .text(`@font-face {
    font-family: 'indie_flowerregular';
    src: url(data:application/font-woff2;charset=utf-8;base64,) format('woff2'),
         url(data:application/font-woff;charset=utf-8;base64,) format('woff');
    font-weight: normal;
    font-style: normal;
	}`);
};

module.exports = {
  addFontGaegu,
  addFontIndieFlower,
};
